body {

    background-color: rgb(200, 214, 243);
    padding: 20px;
    margin: 0;
}

ul.header li {
    /*display horizontal*/
    display: inline;
    list-style-type: none;
    margin: 0;
  }

  ul.header {
    background-color: darkkhaki;
    padding: 0;
  }

  ul.header li a {
    color: firebrick;
    font-weight: bold;
    text-decoration: none;
    padding: 10px;
    display: inline-block;
  }


  .content {
    background-color: #FFF;
    padding: 20px;
  }
  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }

.active {
    background-color: deepskyblue;
}
